.slide {
  width: 400px;
  height: 300px;
  overflow: hidden;
  position: relative;
  background-color: #000;
}
.slide > div {
  width: 100%;
  height: 100%;
  background-size: cover;
  position: absolute;
  -webkit-animation: slide 25s infinite;
          animation: slide 25s infinite;
  opacity: 0;
}
.slide > div:nth-child(2) {
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
}
.slide > div:nth-child(3) {
  -webkit-animation-delay: 10s;
          animation-delay: 10s;
}
.slide > div:nth-child(4) {
  -webkit-animation-delay: 15s;
          animation-delay: 15s;
}
.slide > div:nth-child(5) {
  -webkit-animation-delay: 20s;
          animation-delay: 20s;
}

@-webkit-keyframes slide {
  10% {
    opacity: 1;
  }
  20% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
  40% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

@keyframes slide {
  10% {
    opacity: 1;
  }
  20% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
  40% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

.slide .btn:hover {
  cursor: pointer;
}
